import { useState, useRef, ReactNode } from 'react';
import useConnectionDetails from '../../hooks/connections/useConnectionDetails';
import { useIntersection } from '../../hooks/intersection/useIntersection';

export const ConnectionImage = ({
  dotYouId,
  className,
  fallback,
}: {
  dotYouId: string;
  className?: string;
  fallback?: ReactNode;
}) => {
  const [isInView, setIsInView] = useState(false);
  const wrapperRef = useRef<HTMLDivElement>(null);

  const { data: connectionDetails } = useConnectionDetails({
    dotYouId: isInView ? dotYouId : undefined,
  }).fetch;

  useIntersection(wrapperRef, () => {
    setIsInView(true);
  });

  return (
    <div ref={wrapperRef}>
      {connectionDetails?.profileImage?.url ? (
        <img
          src={connectionDetails.profileImage.url}
          width={connectionDetails.profileImage.naturalSize.width}
          height={connectionDetails.profileImage.naturalSize.height}
          className={className ?? ''}
        />
      ) : fallback ? (
        <>{fallback}</>
      ) : (
        <div className={className ?? ''}></div>
      )}
    </div>
  );
};

export default ConnectionImage;
