import { Link } from 'react-router-dom';
import DarkModeToggle from '../DarkModeToggle/DarkModeToggle';
import ProfileNav from '../../../Auth/ProfileNav/ProfileNav';
import useSiteData from '../../../../hooks/siteData/useSiteData';
import Logo from '../../Icons/Logo/Logo';
import Image from '../../../Image/Image';
import { BuiltInProfiles, getTargetDriveFromProfileId } from '@youfoundation/dotyoucore-js';
import LinkToConsole from './LinkToConsole';
import useAuth from '../../../../hooks/auth/useAuth';
import { t } from '../../../../helpers/i18n/dictionary';
import useTabs from '../../../../hooks/tabs/useTabs';

const Header = () => {
  const { isOwner } = useAuth();
  const { isTabs } = useTabs();
  const { owner } = useSiteData().data ?? {};

  const targetDrive = getTargetDriveFromProfileId(BuiltInProfiles.StandardProfileId);

  return (
    <header className="body-font">
      <div className="container mx-auto flex flex-row flex-wrap items-center px-5 py-3 md:flex-row md:p-5">
        <Link className="title-font flex h-10 items-center font-medium" to="/home">
          {owner?.profileImageId ? (
            <Image
              fileId={owner?.profileImageId}
              targetDrive={targetDrive}
              className="h-12 w-12 rounded-full"
              cover={true}
            />
          ) : (
            <Logo className="h-10 w-10" />
          )}
          <span className="ml-3 hidden text-xl font-normal sm:block">
            {`${owner?.firstName ?? ''} ${owner?.surName ?? ''}`}
          </span>
        </Link>
        <nav className="ml-auto flex flex-wrap items-center justify-center text-base">
          {isOwner ? (
            <>
              {!isTabs ? (
                <>
                  <Link
                    className="mr-5 hover:text-gray-900 dark:hover:text-white"
                    to={'/home/feed'}
                  >
                    {t('Feed')}
                  </Link>
                  <Link
                    className="mr-5 hover:text-gray-900 dark:hover:text-white"
                    to={'/home/about'}
                  >
                    {t('About')}
                  </Link>
                </>
              ) : null}
              <LinkToConsole className="mr-2" />
            </>
          ) : null}
          <DarkModeToggle className="mr-2" />
          <ProfileNav />
        </nav>
      </div>
    </header>
  );
};

export default Header;
